.button-container{
    margin: 20px;
    padding: 20px 0px 20px;
    width: auto;
    display: flex;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    /* display: block; */
    /* align-items: center; */
    /* text-decoration: none; */
    
}

.theme-button{
    /* margin: 20px 20px; */
    /* background: gray; */

    letter-spacing: .3em;

    padding: 5px 10px 5px;
    transition: all .4s ease-out;

    background-repeat: no-repeat;
    background: linear-gradient(90deg,white, white, gray);
    background-position: -50%, 150;
    background-size: 300% 100%;

    text-decoration: none;
    border-style: solid;
    border: 1px solid;
  }
  
.theme-button:hover {   
color: white;
border-color: gray;
/* border: 1px solid rgba(black, 0); */
background-position: 100%, 75%;
}
  

a {
    margin: 20px 20px;
    transition: all .4s ease-out;
    text-decoration: none;
}

a:visited { text-decoration: none; color: black; }
